

export type InitializationStateDialogType = {
    data:DataType []
    message:MessageType[]
}

type DataType = {
    id:number;
    name:string

}

type MessageType = {
    id:number,
    message:string
}


const InitializationState:InitializationStateDialogType = {
    data:[{id:1,name:'Hrgeirr'},
        {id:2,name:'Eirkr'},
        {id:3,name:'Hrgeirr'},
        {id:4,name:'Egill'},
        {id:5,name:'Styrr'}
    ],
    message :[{id:1,message:' slite og slepe for  f endene til  mtes'},
        {id:2,message:' vre i den sjuende himmel'},
        {id:3,message:' drive dank'},
        {id:4,message:' dra ved nesa'},
        {id:5,message:' gjre en mygg til en elefant'}]

}



export  const dialogsReducer= (state=InitializationState ,action:AddPostDialogs):InitializationStateDialogType => {

    switch (action.type) {
        case 'AddPostDialogs':
            let newPost ={id:6,message:action.newDialogs}

            return {...state,message:[...state.message,newPost]}



        default :return state
    }
}

export  type  AddPostDialogs = ReturnType<typeof AddPostDialogsActionCreator>


export  const AddPostDialogsActionCreator = (newDialogs:string)=> {
    return {
        type:'AddPostDialogs',
        newDialogs: newDialogs
    } as const

}
